import React from 'react';
import {BrowserRouter as Router,Redirect,Route,NavLink,Switch} from 'react-router-dom'
import Header from './pages/Header';
import Home from './pages/Home';
import Doc from './pages/Doc';
import Tuto from './pages/Tuto';
import Blog from './pages/Blog';
import Comm from './pages/Comm';
import Login from './pages/Login';
import routes from './router.config';

function PrivateRoute({ component:Com, ...rest }) {
	return (
	  <Route
		{...rest}
		render={({ location }) =>
		   localStorage.getItem('userinfo')
		   ?<Com {...rest}/> 
		   : (
			<Redirect
			  to={{
				pathname: "/login",
				state: location 
			  }}
			/>
		  )
		}
	  />
	);
  }

export const RouteWithSubRoutes = ({routes}) => {
	console.log(routes);
	return routes.map((route) => {
		const {pri,...rest} = route;
		if (pri) return <PrivateRoute {...rest}/>
		return (
			<Route
			path={route.path}
			render={props => (
				<route.component {...props} routes={route.routes} />
			)}
			exact={route.exact}
			/>
		);
	})
}

const App = ()=>{
	return <Router>
		<Header />
		<Switch>
			<RouteWithSubRoutes routes={routes} />
		</Switch>
	</Router>
}

export default App;
